import uniIcons from "../uni-icons/uni-icons.vue";

/**
 * Fav 收藏按钮
 * @description 用于收藏功能，可点击切换选中、不选中的状态
 * @tutorial https://ext.dcloud.net.cn/plugin?id=864
 * @property {Boolean} star = [true|false] 按钮是否带星星
 * @property {String} bgColor 未收藏时的背景色
 * @property {String} bgColorChecked 已收藏时的背景色
 * @property {String} fgColor 未收藏时的文字颜色
 * @property {String} fgColorChecked 已收藏时的文字颜色
 * @property {Boolean} circle = [true|false] 是否为圆角
 * @property {Boolean} checked = [true|false] 是否为已收藏
 * @property {Object} contentText = [true|false] 收藏按钮文字
 * @event {Function} click 点击 fav按钮触发事件
 * @example <uni-fav :checked="true"/>
 */
export default {
	name: "UniFav",
	components: {
		uniIcons
	},
	props: {
		star: {
			type: [Boolean, String],
			default: true
		},
		bgColor: {
			type: String,
			default: "#eeeeee"
		},
		fgColor: {
			type: String,
			default: "#666666"
		},
		bgColorChecked: {
			type: String,
			default: "#007aff"
		},
		fgColorChecked: {
			type: String,
			default: "#FFFFFF"
		},
		circle: {
			type: [Boolean, String],
			default: false
		},
		checked: {
			type: Boolean,
			default: false
		},
		contentText: {
			type: Object,
			default () {
				return {
					contentDefault: "收藏",
					contentFav: "已收藏"
				};
			}
		}
	},
	watch: {
		checked() {
			if (uni.report) {
				if (this.checked) {
					uni.report("收藏", "收藏");
				} else {
					uni.report("取消收藏", "取消收藏");
				}
			}
		}
	},
	methods: {
		onClick() {
			this.$emit("click");
		}
	}
};
